import { useState } from "react";
import { Link } from "react-router-dom";


const Get_Start_CH = () => {
    const [blogs, setBlogs] = useState([
        { title: ' ', place: 'Normal_CH', subtitle: '3D', id: 1 },
        { title: ' ', place: 'Better_CH', subtitle: '', id: 2 },
        { title: ' ', place: 'Book_homepage', subtitle: '', id: 3 },
    ])


    return (
        <div className="Get_Start_CH">
            {blogs.map(blog => (
                <div className="blog-preview" key={blog.id}>
                    <Link to={`/${blog.place}`}>
                        <h1>{blog.title} </h1>
                        <p> {blog.subtitle} </p>
                    </Link>
                </div>
            ))}
        </div>
    );
}

export default Get_Start_CH;