import { useState } from "react";
import { Link } from "react-router-dom";
import Logo from './img/Logo.png';

import { Container } from 'react-bootstrap';
import { Row, Col, Figure, Button, Image, Dropdown, Form } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Community from "./Community";

const Fake_Community_CH = () => {

    const [blogs, setBlogs] = useState([
        { title: '!', place: '', subtitle: '...', id: 1 },
        { title: '!', place: '', subtitle: '...', id: 2 },
        { title: '!', place: '', subtitle: '...', id: 3 },
        { title: 'ITI1120?', place: '', subtitle: ' ...', id: 3 },
    ])
    return (
        <div className="Fake_Community_CH">
            {blogs.map(blog => (
                <div className="blog-preview" key={blog.id}>
                    <Link to={`/Community`}>
                        <h1>{blog.title} </h1>
                        <p> {blog.place} </p>
                        <p> {blog.subtitle} </p>
                    </Link>
                </div>
            ))}
        </div>
    );
}

export default Fake_Community_CH;