import { useState } from "react";
import { Link } from "react-router-dom";
import car from './img/car.jpg';
import { Container } from 'react-bootstrap';
import { Row, Col, Figure, Button, Image, Dropdown, Form } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

const Better_CH = () => {
    const [blogs, setBlogs] = useState([
        { title: '', place: 'Get_Start_CH', subtitle: '', id: 1 ,more:''},
        { title: '', place: 'Better_CH', subtitle: '', id: 2 ,more:''},
        { title: '', place: 'Better_CH', subtitle: '', id: 3 ,more:''},
        { title: '', place: 'Better_CH', subtitle: '', id: 4 ,more:''},
        { title: '', place: 'Better_CH', subtitle: '', id: 5 ,more:''},
        { title: '', place: 'Better_CH', subtitle: '', id: 5 ,more:''},
        { title: '', place: 'Better_CH', subtitle: '', id: 4 ,more:''},
        { title: '', place: '', subtitle: '', id: 5 ,more:''},

    ])

    return (
        <div className="Better_CH">
            <Container fluid>
                <Row>
                    <Col sm={8}>
                        <div className="Image">
                            <img src={car} alt="car" />
                        </div>
                    </Col>
                    <Col sm={4}>
                        {blogs.map(blog => (
                            <div className="blog-preview" key={blog.id} >
                                <Link to={`/${blog.place}`}>
                                    <h2>{blog.title}</h2>
                                    <p>{blog.subtitle}</p>
                                    <p>{blog.more} </p>
                                </Link>
                            </div>
                        ))}
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default Better_CH;